@import '../_utilities.scss';

.srgssr-subdivisions {
  position: absolute;
  bottom: 0;

  z-index: 1;

  width: 100%;
  height: $subdivision-container-height;

  margin-top: 10px;

  overflow: hidden;

  color: #d2d2d2;

  background-color: rgba(0, 0, 0, .7);

  transition: opacity 1s ease-out;
}

.srgssr-subdivisions__arrow {

  position: absolute;
  top: 0;

  display: inline-block;

  width: $subdivision-container-arrow-width;
  height: $subdivision-container-height;

  text-align: center;

  background-color: rgba(0, 0, 0, 1);
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 1);

}

.subdivisions-arrow-next,
.subdivisions-arrow-prev {
  .vjs-icon-placeholder {
    top: 30%;

    &::before {
      font-size: 1.3em;
    }
  }

  color : #fff;
}

.subdivisions-arrow-next {
  right: 0;

  .vjs-icon-placeholder {
    @extend .vjs-icon-arrow-right;
  }
}

.subdivisions-arrow-prev {
  left: 0;

  .vjs-icon-placeholder {
    @extend .vjs-icon-arrow-left;
  }
}

.subdivisions__list {
  display: flex;
  flex-direction: row;

  height: 100%;

  margin: 0 $subdivision-container-arrow-width;

  overflow-x: hidden;
  overflow-y: hidden;

  // hide scroll bar for Safari iPadOs
  &::-webkit-scrollbar {
    display: none;
  }
}

.subdivision {
  width: $subdivision-width;

  text-decoration: none;

  opacity: 0;

  transition: all .8s ease;
}

&.lbw-keyboard-navigation .subdivision--show:focus {
  outline-offset: -2px;

  transition: none;
}

.subdivision--show {
  opacity: 1;
}

  
.subdivision__img {
  width: $subdivision-width;
  height: $subdivision-image-height;

  color: transparent;
}

.subdivision__duration {
  position: absolute;
  top: $subdivision-image-height - 20px;
  right: 5px;

  padding: 2px 3px;

  color: #d2d2d2;
  font-size: $subdivision-font-size;

  background: rgba(0, 0, 0, .7);

  border-radius: 2px;
}

.subdivision__description {

  //Ellipsis: see https://stackoverflow.com/a/41137262
  display: -webkit-box;
  box-sizing: border-box;
  max-width: $subdivision-width;
  height: 48px;

  margin: 0px;
  padding: 5px 6px;
  overflow: hidden;

  color: white;
  font-size: $subdivision-font-size;
  font-family: 'srgssr-type-rg';
  line-height: 1.3;
  white-space: normal;
  text-align: left;
  text-overflow: ellipsis;
  word-wrap: break-word;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.subdivision--selected {
  background-color: $selected-segment;
}

[data-blockReason] {
  cursor: default;

  pointer-events: none;

  .vjs-icon-placeholder {
    position: absolute;
    top:5%;
    left:35%;
    z-index: 100;

    color: white;
    font-size: 1.8em;
    font-family: VideoJS, 'srgssr-type-md';
    vertical-align: middle;
  }

  .subdivision__img {
    opacity: .5;
  }
}

//Allow horizontal scroll on mobile
&.srgssr-touch-enabled {
  // add 'momentum' style scrolling. Overflow need to be on scroll.
  -webkit-overflow-scrolling: touch;

  .subdivisions__list {
    margin: 0;
    overflow-x: scroll;
  }

  .subdivisions-arrow-prev,
  .subdivisions-arrow-next {
    display: none;
  }
}



//Show subdivisions
&.srgssr-has-subdivisions {

  //By default subdivision are shown under the control bar
  .vjs-control-bar {
    bottom: $subdivision-container-height;
  }

  .vjs-srgssr-overlay {
    height: calc(100% - #{$subdivision-container-height});
  }

  &:not(.srgssr-touch-enabled) .srgssr-end-screen {
    padding-bottom: $subdivision-container-height;
  }

  .vjs-tech {
    height: calc(100% - #{$subdivision-container-height});
  }

  .vjs-has-started {
    .vjs-control-bar {
      bottom: 140px;
    }
  }


  //In fullscreen the subdivisions are shown above the control bar
  &.vjs-fullscreen {

    &:not(.vjs-has-started) .srgssr-subdivisions,
    &.vjs-ended .srgssr-subdivisions {
      display: none;
    }

    &.vjs-has-started.vjs-user-inactive.vjs-playing .srgssr-subdivisions {
      opacity: 0;
    }

    &.vjs-has-started.vjs-paused .srgssr-subdivisions,
    &.vjs-has-started.srgssr-touch-enabled .srgssr-subdivisions,
    &.vjs-has-started.vjs-user-active.vjs-playing .srgssr-subdivisions:hover,
    &.vjs-user-active .vjs-control-bar:hover~.srgssr-subdivisions {
      opacity: 1;
    }


    .srgssr-subdivisions {
      bottom: 70px;

      opacity: 0;
    }

    .vjs-control-bar {
      bottom: 0;
    }

    .vjs-srgssr-overlay {
      height: calc(100%);
    }

    .vjs-tech {
      height: calc(100%);
    }
  }

  &:not(.vjs-fullscreen) {
    .srgssr-subdivisions {
      background-color: black;
    }
  }
}
