.switch {
  position: relative;

  display: inline-block;
  width: 27px;
  height: 14px;

  outline: 0;
}

.checkbox {
  display: none;
  width: 0;
  height: 0;

  opacity: 0;
}

.slider {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  background-color: #3f3f3f;
  border-radius: 16px;
  cursor: pointer;

  transition: .4s;
}

.slider:before {
  position: absolute;
  bottom: -1px;
  left: -1px;

  width: 16px;
  height: 16px;

  background-color: #5C5C5C;
  border-radius: 50%;
  box-shadow: -3px 2px 4px rgba(0, 0, 0, 0.15);

  transition: 0.4s;

  content: "";
}

.checkbox:checked+.slider {
  background-color: #fff;
}

.checkbox:checked+.slider:before {
  background-color: #fff;
  transform: translateX(14px);
}
