.srgssr-countdown {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;

  font-size: 35px;

  .timer {
    display: grid;
    grid-template-rows: minmax(0fr, 52px) minmax(0fr, 30px);
    grid-template-columns: repeat(7, 0fr);

    align-items: center;
    align-self: center;
  }

  .description {
    font-size: 25px;
  }

  .flexCentered {
    display: flex;
    justify-content: center;
  }

  .text {
    font-size: .4em;
  }

  .days {
    grid-row: 1;
    grid-column-start: 1;
    grid-column-end: 2;
  }

  .text-days {
    grid-row: 2;
    grid-column-start: 1;
    grid-column-end: 2;
  }

  .hours {
    grid-row: 1;
    grid-column-start: 3;
    grid-column-end: 4;
  }

  .text-hours {
    grid-row: 2;
    grid-column-start: 3;
    grid-column-end: 4;
  }

  .minutes {
    grid-row: 1;
    grid-column-start: 5;
    grid-column-end: 6;
  }

  .text-minutes {
    grid-row: 2;
    grid-column-start: 5;
    grid-column-end: 6;
  }

  .seconds {
    grid-row: 1;
    grid-column-start: 7;
    grid-column-end: 8;
  }

  .text-seconds {
    grid-row: 2;
    grid-column-start: 7;
    grid-column-end: 8;
  }


  .separator-1 {
    grid-row: 1;
    grid-column-start: 2;
    grid-column-end: 3;
  }

  .separator-2 {
    grid-row: 1;
    grid-column-start: 4;
    grid-column-end: 5;
  }

  .separator-3 {
    grid-row: 1;
    grid-column-start: 6;
    grid-column-end: 7;
  }

  [class*='separator'] {
    max-width: 10px;
  }


  .digit {
    display: inline;

    width: 35px;

    margin: 2px;

    padding: 0 7px 2px 7px;

    background-color: rgba(0, 0, 0, 0.7);

    border-radius: 2px;
  }

  .invisible {
    display: none;
  }
}

&.vjs-layout-x-large .srgssr-countdown,
&.vjs-layout-huge .srgssr-countdown,
&.vjs-layout-large .srgssr-countdown,
&.vjs-layout-medium .srgssr-countdown {
  font-size: 45px;

  .timer {
    grid-template-rows: minmax(0fr, 42px) minmax(0fr, 35px);

  }

  .digit {
    width: 45px;

    padding: 0 9px 2px 9px;
  }
}

&.vjs-layout-small .srgssr-countdown {
  font-size: 25px;

  .timer {
    grid-template-rows: minmax(0fr, 32px) minmax(0fr, 27px);

  }

  .digit {
    width: 25px;

    padding: 0 4px 2px 4px;
  }

  .description {
    font-size: 20px;
  }
}

&.vjs-layout-tiny .srgssr-countdown,
&.vjs-layout-x-small .srgssr-countdown {
  font-size: 15px;

  .timer {
    grid-template-rows: minmax(0fr, 24px) minmax(0fr, 20px);
  }

  .digit {
    width: 15px;

    padding: 0;
  }

  .description {
    font-size: 15px;
  }
}
