.srg-title {
  display: -webkit-box;
  flex-flow: row wrap;
  flex-grow: 1;
  justify-content: flex-start;

  overflow: hidden;

  font-weight: normal;
  font-size: 1.5em;
  line-height: 1.5em;

  white-space: normal;
  text-overflow: ellipsis;
  word-wrap: break-word;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
