//https://css-tricks.com/viewport-sized-typography/
.srgssr-end-screen {
  position: absolute;
  top: 0;
  z-index: -1;

  display: block;
  width: 100%;

  height: 100%;

  background-color: rgba(0, 0, 0, .6);

  opacity: 0;

  transition: opacity 1s ease, z-index .5s ease;
}

.srgssr-end-screen-feature {
  width: 100%;
  height: 100%;
  padding-bottom: 70px;
}

//Description is enabled by default on touch devices
&.srgssr-touch-enabled .item__description {
  opacity: 1;
}

.srgssr-end-screen--show {
  z-index: 0;

  transform: scale(1);
  opacity: 1;
}

&.vjs-live {
  .srgssr-end-screen {
    display: none;
  }
}
