&.video-js .srgssr-warning-message {
  z-index: 2;

  display: flex;
  justify-content: center;

  height: auto;

  margin-top: 20px;
  padding: 0 10px;

  background: transparent;
  outline: none;
}

.srgssr-warning-message {
  .warning-message__text {
    position: relative;

    width: auto;

    margin: 0;
    padding: 10px 14px;

    font-size: initial;

    font-family: 'srgssr-type-rg';
    text-align: center;

    background: #0F5ACB;
    border-radius: 4px;
  }
}
