.srg-chromecast-control {
  order: 3;

  cursor: pointer;
}

.srg-chromecast-control .vjs-icon-placeholder {
  flex: none;
  @extend .vjs-icon-chromecast;
}

.srg-chromecast-control.srg-chromecast-active .vjs-icon-placeholder {
  @extend .vjs-icon-chromecast-active;
}

&.video-js.chromecast--connected {
  .vjs-tech-chromecast {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    display: flex;
    height: auto;
    margin: 0;
    padding: 0;

    vertical-align: middle;

    background-color: #000000;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
    cursor: pointer;
  }

  .vjs-tech-chromecast-container{

    display:flex;
    align-items: center;
    align-self: flex-end;
    width: 100%;
    margin-bottom: 70px;
    padding-left : 25px;

    .vjs-icon-chromecast-active{
      font-size:27px;
    }
    .vjs-tech-chromecast-title {
      margin: 0 0 0 10px;

      font-size: 20px;
    }
  }
}

&.chromecast--connected.vjs-has-started.vjs-show-big-play-button-on-pause {
  .vjs-srgssr-overlay,
  .vjs-subs-caps-button,
  .vjs-text-track-display,
  .vjs-audio-button,
  .srg-settings-menu-button,
  .srgssr-end-screen {
    display: none;
  }

  .vjs-control-bar{
    bottom: 0;
  }
}

.vjs-tech-chromecast {
  background-color: #000000;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
}
