@import './_variables.scss';
@import './_utilities.scss';

@font-face {
  font-family: 'srgssr-type-md';
  src: url(../fonts/SRGSSRTypeText_W_Md.woff);
}

@font-face {
  font-family: 'srgssr-type-rg';
  src: url(../fonts/SRGSSRTypeText_W_Rg.woff);
}

.vjs-srgssr-skin {
  overflow: hidden;

  font-family: srgssr-type-md;

  transform: scale(1);

  *:focus {
    outline: 0;
  }

  .vjs-button>.vjs-icon-placeholder:before {
    font-size: 2.2em;
    line-height: 1.67;
  }

  //Disable cursor when user inactive
  &.vjs-user-inactive {
    cursor: none;
  }

  .vjs-control:focus::before,
  .vjs-control:hover::before,
  .vjs-control:focus,
  .vjs-slider:focus {
    text-shadow: none;
  }

  &.lbw-keyboard-navigation :focus {
    outline: 2px solid $srg-red;
    box-shadow: none;
  }

  @import './components/big-play-button.scss';
  @import './components/seek-to-live-button.scss';
  @import './components/overlay.scss';
  @import './components/end-screen.scss';
  @import './components/end-screen-continuous-playback.scss';
  @import './components/end-screen-recommendations.scss';
  @import './components/end-screen-replay.scss';
  @import './components/error-display.scss';
  @import './components/control-bar.scss';
  @import './components/SRGSSR-button.scss';
  @import './components/header.scss';
  @import './components/sharing.scss';
  @import './components/header-title.scss';
  @import './components/fullscreen-button.scss';
  @import './components/chromecast-toggle.scss';
  @import './components/loading.scss';
  @import './components/stop-button.scss';
  @import './components/subdivisions.scss';
  @import './components/warning-message.scss';
  @import './components/poster.scss';
  @import './components/count-down.scss';
  @import './components/switch-button.scss';
  @import './components/airplay-button.scss';
  @import './components/playback-rate.scss';
  @import './components/text-track-display.scss';
  @import './components/image-copyright.scss';
}
