.vjs-text-track-display__container {
  position: absolute;

  width: 100%;

  padding: 12px;

  font-family: sans-serif;
  line-height: 1.2;

  white-space: pre-line;
  text-align: center;

  writing-mode: horizontal-tb;
}

&.vjs-tiny,
&.vjs-layout-x-small,
&.vjs-layout-small,
&.vjs-layout-medium {
  .vjs-text-track-display__container {
    font-size: 15px;
  }
}

&.vjs-layout-large,
&.vjs-layout-x-large,
&.vjs-layout-huge {
  .vjs-text-track-display__container {
    font-size: 25px;
  }
}


.vjs-text-track-display__cue {
  display: inline-block;
  padding: 2px 4px;

  unicode-bidi: plaintext;

  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 2px;
}
