// Name 	  Class                 Min. Width  Max. Width
// tiny 	  vjs-layout-tiny 	    0 	        224
// xsmall 	vjs-layout-x-small 	  225 	      304
// small 	  vjs-layout-small 	    305 	      384
// medium 	vjs-layout-medium 	  385 	      464
// large 	  vjs-layout-large 	    465 	      624
// xlarge 	vjs-layout-x-large 	  625 	      2560
// huge 	  vjs-layout-huge 	    2561 	      Infinity

.video-js:not(.vjs-fullscreen):not(.srgssr-audio-source),
.video-js.vjs-fullscreen:not(.srgssr-audio-source) {

  &.vjs-layout-small,
  &.vjs-layout-x-small,
  &.vjs-layout-tiny {

    .vjs-current-time,
    .vjs-time-divider,
    .vjs-duration,
    .vjs-playback-rate,
    .vjs-chapters-button,
    .vjs-descriptions-button,
    .vjs-captions-button,
    .vjs-subtitles-button,
    .vjs-audio-button,
    .vjs-volume-control {
      display: initial;
    }

    .vjs-progress-control {
      display: inherit;
    }

    .vjs-playback-rate,
    .vjs-custom-control-spacer {
      display: none;
    }
  }

  &.vjs-layout-x-small {
    .vjs-volume-control {
      display: none;
    }
  }

  &.vjs-layout-large,
  &.vjs-layout-medium,
  &.vjs-layout-small,
  &.vjs-layout-x-small,
  &.vjs-layout-tiny {
    .vjs-srgssr-button {
      display: none;
    }
  }

  &.vjs-layout-medium,
  &.vjs-layout-small,
  &.vjs-layout-x-small,
  &.vjs-layout-tiny {

    .srg-chromecast-control,
    .vjs-picture-in-picture-control {
      display: none;
    }
  }

  &.vjs-layout-small,
  &.vjs-layout-x-small,
  &.vjs-layout-tiny {

    .srg-menu__subtitles-size-selector,
    .settings-menu,
    .vjs-audio-button {
      display: none;
    }
  }

  &.vjs-layout-x-small,
  &.vjs-layout-tiny {

    .subtitles-menu,
    .vjs-volume-panel {
      display: none;
    }
  }

  &.vjs-layout-tiny {

    .vjs-duration,
    .vjs-time-divider {
      display: none;
    }

    .vjs-time-control {
      flex-grow: 1;

      text-align: center;
    }
  }
}

.video-js:not(.vjs-fullscreen).vjs-live,
.video-js.vjs-fullscreen.vjs-live {

  &.vjs-layout-small,
  &.vjs-layout-x-small,
  &.vjs-layout-tiny {

    .vjs-time-divider,
    .vjs-duration {
      display: none;
    }
  }

  &.vjs-layout-x-small,
  &.vjs-layout-tiny {

    .vjs-progress-control {
      display: inherit;
    }
  }

  &:not(.vjs-liveui) {
    .vjs-current-time {
      display: none;
    }

    .vjs-live-control {
      justify-content: flex-end;
    }
  }

  .vjs-playback-rate {
    display: none;
  }
}

//Audio Player Layout
.video-js.srgssr-audio-source {

  &.vjs-layout-large,
  &.vjs-layout-medium,
  &.vjs-layout-small,
  &.vjs-layout-x-small,
  &.vjs-layout-tiny {
    .vjs-progress-control {
      display: inherit;
    }

    .vjs-custom-control-spacer,
    .vjs-srgssr-button {
      display: none;
    }

    &:not(.vjs-live) .vjs-time-control:not(.vjs-remaining-time) {
      display: initial;
    }

    .playback-rate-menu {
      flex-grow: 1;
    }
  }

  &.vjs-layout-small,
  &.vjs-layout-x-small,
  &.vjs-layout-tiny {
    .playback-rate-menu {
      display: none;
    }

    //NB: The vjs-volume-control is hidden by videojs css rules 
    //when the size is smaller than medium  
    .vjs-volume-panel.vjs-hover,
    .vjs-volume-panel.vjs-volume-panel-horizontal.vjs-hover:active {
      width: initial;
    }
  }

  &.vjs-layout-x-small,
  &.vjs-layout-tiny {
    .vjs-icon-rewind.vjs-control-seek,
    .vjs-icon-forward.vjs-control-seek {
      display: none;
    }
  }

  &.vjs-layout-tiny {
    .srg-chromecast-control {
      display: none;
    }
  }
}

//Touch devices
.video-js.srgssr-touch-enabled {
  .vjs-volume-panel,
  .vjs-volume-panel.vjs-volume-panel-horizontal:active {
    width: initial;
  }

  .vjs-volume-control {
    display: none;
  }
}
