$image-copryright-bottom: 17px;

.image-copyright {
  position: absolute;
  
  bottom: $image-copryright-bottom;
  bottom: $controlBarHeight + $image-copryright-bottom;

  display: none;
  
  margin: 0 10px;
  padding: 0 18px;

  font-size: 1.4em;
  font-family: 'srgssr-type-rg';
}

&.vjs-has-started.srgssr-audio-source {
  .image-copyright {
    display: block;
  }
}

&.vjs-has-started.srgssr-audio-source.srgssr-has-subdivisions {
  .image-copyright {
    bottom: $subdivision-container-height + $controlBarHeight + $image-copryright-bottom;
  }
}
