
.vjs-loading-spinner {
  position: absolute;
  top: 50%;
  left: 50%;

  display: none;
  box-sizing: border-box;
  width: 50px;
  height: 50px;
  margin: -25px 0 0 -25px;

  text-align: left;

  background-color: transparent;
  background-clip: padding-box;

  border: none;
  visibility: hidden;
  opacity: 0.85;

  &:after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    border: 4px solid $colorWhite !important;
    border-top-color: $colorWhite !important;
    border-right-color: $colorBlack30 !important;
    border-bottom-color: $colorBlack30 !important;

    border-radius: 50%;

    content: "";
  }
}

&.vjs-seeking .vjs-loading-spinner,
&.vjs-waiting .vjs-loading-spinner {
  display: block;

  animation: vjs-spinner-show 0s linear 0.3s forwards;
}

&.vjs-seeking .vjs-loading-spinner:after,
&.vjs-waiting .vjs-loading-spinner:after {
  -webkit-animation: vjs-spinner-spin 1.1s cubic-bezier(0.6, 0.2, 0, 0.8) infinite;
  animation: vjs-spinner-spin 1.1s cubic-bezier(0.6, 0.2, 0, 0.8) infinite;
}

&.vjs-seeking .vjs-loading-spinner:after,
&.vjs-waiting .vjs-loading-spinner:after {
  -webkit-animation-delay: 0.44s;
  animation-delay: 0.44s;
}

&:not(.srgssr-touch-enabled).srgssr-has-subdivisions .vjs-loading-spinner {
  top: calc(50% - #{$controlBarHeight});
}

@keyframes vjs-spinner-show {
  to {
    visibility: visible;
  }
}

@-webkit-keyframes vjs-spinner-show {
  to {
    visibility: visible;
  }
}

@keyframes vjs-spinner-spin {
  100% {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes vjs-spinner-spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
