@import '../_utilities.scss';

.vjs-container-header {
  position: absolute;
  top: 0;
  z-index: 3;

  display: flex;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 10px 0 20px;

  line-height: 1.5em;

  background: linear-gradient(to top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
}

&.vjs-has-started .vjs-container-header {
  top: 0;

  visibility: visible;
  opacity: 1;

  transition-timing-function: ease-out;
  @include setTransitionToTop(0.5s, 0.5s, 0.3s);
}

&.vjs-has-started.vjs-user-inactive.vjs-playing:not(.srgssr-audio-source) .vjs-container-header {
  top: -60px;

  visibility: visible;
  opacity: 0;

  transition-timing-function: ease-out;
  @include setTransitionToTop(1s, 1s, 2s);
}

&.vjs-error .vjs-container-header,
&.vjs-ended .vjs-container-header {
  display: none;
}
