@import '../_utilities.scss';

.vjs-srgssr-overlay {
  position: absolute;
  top: 0 !important;
  left: 0 !important;

  display: block;
  width: 100%;
  height: 100%;
  padding: 0;

  font-size: 5em;
  line-height: 1.5em;

  border: none !important;
  border-radius: 0 !important;
}

&.vjs-has-started.vjs-paused.vjs-show-big-play-button-on-pause .vjs-srgssr-overlay {
  display: block;
}

&.vjs-show-big-play-button-on-pause:not(.vjs-has-started) .vjs-srgssr-overlay,
&.first-play .vjs-srgssr-overlay  {
  display: block!important;
}



