$bg-opacity: .4;

&:hover .vjs-big-play-button,
& .vjs-big-play-button:focus,
.vjs-big-play-button {
  background-color: rgba(0, 0, 0, 0.2);
}

.vjs-big-play-button {
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
  padding: 0;

  font-size: calc(0.85em + 4vw);
  //background-color: initial;
  background-color: rgba(0,0,0, $bg-opacity);
  border: none;
  border-radius: 0;

  .vjs-icon-placeholder:before {
    top: 50%;
    left: 50%;

    width: auto;
    height: auto;

    transform: translate(-50%, -50%) !important;
    opacity: 1;
  }
}

&.video-js:hover .vjs-big-play-button,
&.video-js .vjs-big-play-button:focus {
  //background-color: initial;
  background-color: rgba(0,0,0, $bg-opacity);
}

&.vjs-paused.vjs-has-started .vjs-big-play-button .vjs-icon-placeholder:before {
  @extend .vjs-icon-pause;
}
&.vjs-playing.vjs-has-started .vjs-big-play-button .vjs-icon-placeholder:before {
  @extend .vjs-icon-play;
}

&.vjs-paused.vjs-live.vjs-has-started:not(.vjs-liveui) .vjs-big-play-button .vjs-icon-placeholder:before {
  @extend .vjs-icon-stop;
}

&.vjs-has-started .vjs-big-play-button {
  display: block;
}

&.first-play .vjs-big-play-button {
  display: block!important;
}


&.vjs-has-started.vjs-paused .vjs-big-play-button {
  display: block;

  animation: fadePause .5s linear forwards;

  .vjs-icon-placeholder {
    display: block;

    animation: fadeScalePause .5s linear forwards;
  }
}

&.vjs-has-started.vjs-playing .vjs-big-play-button {
  display: block;

  animation: fadePlay .5s linear forwards;

  .vjs-icon-placeholder {
    display: block;

    animation: fadeScalePlay .5s linear forwards;
  }
}

.video-js .vjs-big-play-button .vjs-icon-placeholder:before,
.vjs-button>.vjs-icon-placeholder:before {
  position: relative;
}

@keyframes fadeScalePause {
  0% {
    opacity: 1;
  }
  to {
    transform: scale(2);
    opacity: 0
  }
}

@keyframes fadeScalePlay {
  0% {
    opacity: 1;
  }
  to {
    transform: scale(2);
    opacity: 0
  }
}

@keyframes fadePause {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadePlay {
  0% {
    opacity: .6;
  }
  to {
    opacity: 0;
  }
}
