.srgssr-recommendation-grid {
  position: relative;

  display:grid;
  grid-gap: 5px;

  align-content: center;

  .item {
    position: relative;

    width: auto;
    max-height: 140px;

    text-decoration: none;

    transform: scale(0);

    transition: transform .1s ease;

    &:hover .item__description {
      opacity: 1;
    }
  }

  .item__description {
    position: absolute;
    top: 0;
    z-index: 1;

    width: 100%;
    height: 100%;

    background-image: linear-gradient(to bottom, rgba(12, 12, 12, 0.8) 0, transparent 140px);

    opacity: 0;

    transition: opacity .3s ease;
  }


  .item__img {
    width: 100%;
    max-height: 135px;
    object-fit: cover;
  }

  .item__duration {
    position: absolute;
    right: 5px;
    bottom: 5px;

    padding: 2px 3px;

    color: #d2d2d2;
    font-size: 1.1em;

    background: rgba(0, 0, 0, 0.7);
    border-radius: 2px;
  }

  .item__title,
  .item__show {
    box-sizing: border-box;
    margin: 0;

    font-size: 1.6em;
    font-family: "srgssr-type-rg";
    line-height: 1.2;
    white-space: normal;
    text-align: left;
  }

  .item__title {
    display: -webkit-box;
    padding: 5px 6px 0 6px;
    overflow: hidden;

    color: white;
    font-size: 1.5em;
    white-space: normal;
    text-overflow: ellipsis;
    word-wrap: break-word;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  .item__show {
    padding: 5px 6px;

    color: lightgray;
    font-size: 1.4em;
  }
}

//Description is enabled by default on touch devices
&.srgssr-touch-enabled .item__description {
  opacity: 1;
}

.srgssr-end-screen-feature--show {
  .item {
    transform: scale(1);
  }
}