.srg-replay-button {
  display: block;

  width: 100%;
  height: 100%;
  padding: 0;

  font-size: calc(0.85em + 2vw);
}

.srgssr-replay {
  display: flex;

  width: 100%;
  height: 100%;
}

&.vjs-layout-x-small .srg-replay-button{
  font-size: calc(0.85em + 1.2vw);
}
&.vjs-layout-small .srg-replay-button{
  font-size: calc(0.85em + 1.8vw);
}