@import '../_utilities.scss';

.vjs-modal-dialog .vjs-modal-dialog-content {
  padding: 0;

  font-size: 18px;
}

.vjs-modal-dialog {
  overflow: auto;

  background: rgba(0, 0, 0, .6);
}

.vjs-error-display:before {
  content: none;
}

.vjs-error-display .vjs-modal-dialog-content .content .icon {
  margin-bottom: 10px;

  font-size: 2em;
}

.vjs-error-display .vjs-modal-dialog-content .content {
  position: relative;
  top: 50%;

  display: block;
  width: 100%;
  margin-top: -0.5em;
  padding: 5px;

  color: #fff;
  font-size: 1.5em;
  font-family: VideoJS, 'srgssr-type-rg';
  line-height: 1;
  text-shadow: 0.05em 0.05em 0.1em #000;

  transform: translateY(-50%);
}

&.vjs-layout-tiny .vjs-error-display .vjs-modal-dialog-content .content .icon,
&.vjs-layout-x-small .vjs-error-display .vjs-modal-dialog-content .content .icon {
  margin-bottom: 10px;
}

&.vjs-layout-tiny .vjs-error-display .vjs-modal-dialog-content .content .text {
  display: none;
}

.error__href {
  color: white;
  text-decoration: none;
}
