@import '../_variables.scss';

.sharing-module {
  position: absolute;
  top: 16px;
  right: 16px;

  pointer-events: none;
}

.sharing-container {
  position: relative;
  z-index: 1;

  min-width: 352px;

  padding: 14px 12px 37px 12px;

  background: rgba(22, 22, 22, 0.95);
  border-radius: 4px;
  transform-origin: right top;
  backface-visibility: hidden;
  visibility: visible;

  transition: all .3s cubic-bezier(.47, 1.55, .5, 1);

  pointer-events: all;
}

.sharing-container--hidden {
  transform: scale(0);
  visibility: hidden;
}

.sharing-module__link {
  position: absolute;
  top: 0;
  right: 0;

  width: 1.5em;
  height: 1.5em;

  color: white;
  font-size: 2em;
  text-decoration: none;

  cursor: pointer;

  pointer-events: all;
}

.sharing-container__close {
  position: absolute;
  top: 10px;
  right: 16px;

  z-index: 1;

  display: block;
  width: auto;

  color: white;
  font-size: 1.5em;
  text-decoration: none;

  cursor: pointer;
}

.sharing-container__buttons {
  display: flex;

  flex-wrap: wrap;

  align-items: center;
  justify-content: center;
}

.sharing-container__title {
  margin: 0 0 6px 0;

  font-size: 1.5em;

  backface-visibility: hidden;
}

.sharing-social__action {
  display: flex;

  flex: 0 0 25%;

  flex-direction: column-reverse;
  align-items: center;

  margin: 16px 0 0 0;

  cursor: pointer;

  &:hover {
    .sharing-social__action__link {
      background-color: #333333;
      transform: scale(1.25);
    }
  }
}

.sharing-social__action__title {
  display: block;

  margin-top: 8px;

  font-size: 11px;
  line-height: 14px;
}

.sharing-social__action__link {
  display: block;

  width: 40px;
  height: 40px;

  color: white;
  font-size: 2em;

  line-height: 40px;
  text-decoration: none;

  background-color: #333333;
  border-radius: 50%;
  transform: scale(1);
  backface-visibility: hidden;

  transition: all .3s;
}

.sharing-social__action {
  .sharing-social__action__link--clicked {
    background-color: #fff;
  }
  &:hover {
    .sharing-social__action__link--clicked {
      background-color: #fff;
    }
  }
}

.sharing-social__iframe-action {
  width: 100%;

  padding: 8px 12px;

  color: white;
  font-size: 1.1em;

  background-color: #333333;

  border: none;
  border-radius: 4px;

  box-shadow: none;

  &:hover,
  &:active {
    text-align: center;

    background-color: #232323;
    cursor: pointer;
  }

  &:active {
    color: #333333;
  }
}

//Sharing options
.sharing-container__options {
  display: flex;

  margin-top: 17px;
  margin-bottom: 26px;
}

.sharing-option__label {
  display: flex;
  align-items: center;

  width: auto;

  margin-right: 10px;

  font-size: 11px;
  line-height: 14px;
}

.sharing-option__input {
  position: absolute;
  z-index: -1;

  opacity: 0;

  &:checked~.sharing-option__radio-button::after {
    opacity: 1;
  }
}

.sharing-option__radio-button {
  position: relative;

  display: inline-block;

  width: 12px;
  height: 12px;

  margin-right: 3px;

  background: #333;

  border: 1px solid #ffffff;
  border-radius: 50%;

  &::after {
    position: absolute;
    top: 2px;
    left: 2px;

    width: 6px;
    height: 6px;

    background: #ffffff;
    border-radius: 50%;

    opacity: 0;

    transition: ease .2s;

    content: '';
  }
}

.sharing-option--disable {
  color: #333333;

  user-select: none;

  .sharing-option__radio-button {
    border-color: #333333;
  }
}

&:not(.vjs-fullscreen).vjs-layout-tiny {
  .sharing-module {
    display: none;
  }
}

&:not(.vjs-fullscreen).vjs-layout-x-small {
  .sharing-container__options {
    display:none;
  }
}

&:not(.vjs-fullscreen).vjs-layout-x-small,
&:not(.vjs-fullscreen).vjs-layout-small {
  .sharing-social__action__title {
    display:none;
  }
}

&:not(.vjs-fullscreen).vjs-layout-x-small,
&:not(.vjs-fullscreen).vjs-layout-small,
&:not(.vjs-fullscreen).vjs-layout-medium {
  .vjs-container-header {
    position: static;
  }

  .sharing-module {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    z-index: 3;

    display: flex;
    align-items: center;
    height: 100%;
    padding:6px 0;
  
    &.sharing-module--opened {
      background: rgba(22, 22, 22, 0.95);

      pointer-events: all;

      .sharing-module__link {
        display: none;
      }
    }

    .sharing-container__options {
      margin:11px 0;
    }

    .sharing-module__link {
      top: 16px;
      right: 16px;
    }

    .sharing-container {
      width: 100%;
      min-width: 100%;
      padding-top:0;
      padding-bottom: 0;

      background: none;
    }

    .sharing-container__title {
      margin-top: 0;
    }

    .sharing-container__close {
      top:0;
    }
  }
}

&:not(.vjs-fullscreen).srgssr-has-subdivisions.vjs-layout-tiny,
&:not(.vjs-fullscreen).srgssr-has-subdivisions.vjs-layout-medium,
&:not(.vjs-fullscreen).srgssr-has-subdivisions.vjs-layout-x-small,
&:not(.vjs-fullscreen).srgssr-has-subdivisions.vjs-layout-small {
  .sharing-module {
    padding-bottom: $subdivision-container-height;
  }
}
