.vjs-seek-to-live-control {
  display: flex;
  grid-row: 1;
  grid-column-start: 3;
  grid-column-end: 4;
  align-items: center;
  justify-content: center;
  justify-items: end;
  min-width: initial;
  margin-left: 12px;
  padding: 0;

  font-family: srgssr-type-md;
  line-height: initial;

  .vjs-icon-placeholder{
    display: none;
  }
}

.vjs-seek-to-live-text{
  z-index: 1;

  margin-right: 3px;
  padding: 2px 4px;

  background-color: gray;
  border-radius: 2px;
}

.vjs-seek-to-live-control.vjs-control.vjs-at-live-edge {
  .vjs-icon-placeholder {
  color: white;
  }
  .vjs-seek-to-live-text {
    background-color: #BE111A;
  }
}
