.vjs-control-bar {
  position: absolute;
  bottom: 0;

  flex-wrap: wrap;
  width: 100%;
  height: $controlBarHeight;
  padding: 0 18px;

  color: #fff;

  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
  opacity: 1;
}

//control bar animation
&.vjs-has-started {
  .vjs-control-bar {
    bottom: 0;

    visibility: visible;
    opacity: 1;

    transition-timing-function: ease-out;
    @include setTransitionToBottom(0.5s, 0.5s, 0.3s);
  }
}

&.vjs-has-started.vjs-user-inactive.vjs-playing:not(.srgssr-audio-source),
&:not(.vjs-has-started) {
  .vjs-control-bar {
    bottom: -80px;

    visibility: visible;
    opacity: 0;

    transition-timing-function: ease-out;
    @include setTransitionToBottom(1s, 1s, 2s);
  }
}

&.vjs-has-started.vjs-user-inactive.vjs-playing.srgssr-audio-source .vjs-control-bar {
  opacity: 1;
}

//Control elements
.vjs-control {
  order: 2;

  height: auto;

  outline: 0;
}

//progress bar
.vjs-progress-control {
  flex: 0 0 100%;
  order: 0;
  height: 20px;

  //remove default zoom effect on hover
  &:hover .vjs-progress-holder {
    font-size: inherit;
  }
}

.vjs-progress-holder {
  height: 0.37em;
}

.vjs-progress-holder,
.vjs-load-progress,
.vjs-play-progress {
  border-radius: $progressBorderRadius;
}

.vjs-progress-holder,
.vjs-load-progress {
  background-color: $colorWhite30;
}

.vjs-play-progress {
  background-color: $colorWhite;
}


//Avoid showing double tooltip
&:not(.vjs-scrubbing) .vjs-progress-control:hover .vjs-slider-bar .vjs-time-tooltip {
  display: none;
}

& .vjs-progress-control:hover .vjs-time-tooltip,
& .vjs-progress-control:hover .vjs-progress-holder:focus .vjs-time-tooltip {

  padding: 6px 8px 5px 8px;

  color: $colorBlack;
  font-size: 1.2em;
  font-family: srgssr-type-md;

  background-color: $colorWhite;
}

//Scrubber size
.vjs-play-progress::before {
  top: -0.42em;

  font-size: 1.9em;
}

//remaining time is removed as it's not used
.vjs-remaining-time {
  display: none;
}

//current time, divider and duration default
.vjs-time-control {
  flex-grow: 1;
  align-self: center;

  order: 2;

  font-size: 1.2em;
}


&.vjs-live {
  .vjs-current-time{
    padding-left: 0;
  }

  .vjs-current-time-display {
    display: flex;

    justify-content: flex-start;
  }

  //Clock icon
  .vjs-current-time-display:before {

    margin-right: 6px;

    font-weight: normal;

    font-size: 1em;
    font-family: VideoJS;
    font-style: normal;

    content: "\f12e";
  }
}

&:not(.vjs-live) {

  .vjs-current-time,
  .vjs-duration,
  .vjs-time-divider {
    display: block;
    padding: 0;
  }

  .vjs-current-time {
    flex-grow: 0;

    text-align: right;
  }

  .vjs-duration {
    text-align: left;
  }

  .vjs-time-divider {
    flex: 0 0 0;
    align-self: center;
    //avoid expanding
    min-width: auto;
    padding: 0 4px;
  }
}

//volume
& .vjs-volume-bar {
  margin: 2em 0.1em;
}

.vjs-volume-bar.vjs-slider-horizontal,
.vjs-slider-horizontal .vjs-volume-level {
  height: 0.2em;
}

//volume scrubber
.vjs-slider-horizontal .vjs-volume-level::before {
  top: -0.4em;
  right: -0.5em;

  font-size: 1.2em;
}

//subtitles
.vjs-text-track-display {
  bottom: $controlBarHeight;

  transition: bottom 0.3s;
  transition-timing-function: ease-out;
}

&.vjs-has-started.vjs-user-inactive.vjs-playing,
&:not(.vjs-has-started) {
  .vjs-text-track-display {
    transition: bottom 2s;
  }
}

&.srgssr-has-subdivisions:not(.vjs-fullscreen),
&.srgssr-has-subdivisions.srgssr-touch-enabled {
  .vjs-text-track-display {
    bottom: $subdivision-container-height + $controlBarHeight;
  }

  &.vjs-user-inactive.vjs-playing .vjs-text-track-display {
    bottom: $subdivision-container-height;
  }
}

&.srgssr-has-subdivisions.vjs-fullscreen.srgssr-subdivisions--hover:not(.srgssr-touch-enabled) {
  .vjs-text-track-display {
    bottom: $subdivision-container-height + $controlBarHeight;
  }
}

//Live
.vjs-live-control {
  align-items: center;
  justify-content: end;

  font-family: srgssr-type-md;
}

.vjs-seek-to-live-control {
  order: 1;
}

.vjs-live-display {
  margin: 0 10px 0 0;
  padding: 2px 4px;

  line-height: initial;

  background-color: #BE111A;
  border-radius: 2px;
}

&.vjs-live.vjs-liveui .vjs-current-time {
  display: block;
}

&.vjs-liveui .vjs-progress-control {
  flex: 0 0 calc(100% - 5em);
}

&.vjs-live:not(.vjs-liveui) .vjs-control-bar {
  padding-top: 20px;
}

//Menu settings
.srg-menu {
  position: relative;
  z-index: 2;

  display: flex;
}

.srg-menu__overlay {
  position: fixed;
  top:0;
  left:0;
  z-index: 1;

  display: none;

  width:100%;
  height:100%;

  background-color: rgba(0,0,0,0);
}

&:not(.srgssr-touch-enabled) .srg-menu--open .srg-menu__overlay {
  display: block;
}

.srg-menu--open .srg-menu__container {
  z-index: 2;
}

&:not(.srgssr-touch-enabled) .srg-menu--open .srg-menu__button {
  z-index: 2;
}

.srg-menu__panel-header {
  display: flex;
  align-items: baseline;
  justify-content: flex-start;

  margin-bottom: 10px;
  padding-bottom: 10px;
}

.srg-menu__subtitles-size-selector {
  button {
    padding: 0 4px;

    color: rgba(255, 255, 255, .5);

    cursor: pointer;
  }

  .size-selector-item--1 {
    font-size: 0.8em;
  }

  .size-selector-item--2 {
    font-size: 1em;
  }

  .size-selector-item--3 {
    font-size: 1.2em;
  }

  .srg-menu__subtitles-size-selector__item--selected {
    color: white;
    text-decoration: underline;
  }
}

.srg-menu__panel-back {
  display: flex;
  flex-grow: 1;
  margin-right: 40px;

  button {
    width: auto;
    margin: 0;
    padding: 0;

    font-weight: bold;

    text-align: left;

    cursor: pointer;

    &::before {
      padding-right: 8px;

      font-weight: normal;

      font-size: .6em;
      font-family: VideoJS;
      font-style: normal;
      text-align: left;
      text-align: left;

      content: "\f126";
    }
  }
}

.srg-menu__panel-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 16px;
}

.srg-menu__panel-item--list {
  display: flex;

  flex-direction: column;
  align-items: flex-start;
  justify-content: left;

  width: 100%;

  margin-right: 50px;

  overflow-y: auto;

  scrollbar-color: #5C5C5C transparent;
  scrollbar-width: thin;

  &::-webkit-scrollbar {
    width: 5px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #5C5C5C;
    border-radius: 4px;
  }

  button {
    display: flex;
    align-items: center;

    margin: 0 0 10px 13px;
    padding: 0;

    cursor: pointer;
  }

  .srg-menu__panel-item-label{
    letter-spacing: 0.2px;
  }

  .srg-menu__panel-item--selected {
    .srg-menu__panel-item-label {
      padding: 0 0 2px 0;

      font-weight: bold;
      letter-spacing: 0;

      border-bottom: solid 2px;
      border-radius: 5%;
    }
  }

  .srg-menu__panel-item-badge {
    margin-left: 6px;

    font-size: .66em;

    &.vjs-icon-audio-description {
      font-size: 1.3em;
    }
  }
}

.srg-menu__panel-item__link {
  padding: 0;

  cursor: pointer;

  &::after {
    padding-left: 8px;

    font-weight: normal;

    font-size: .6em;
    font-family: VideoJS;
    font-style: normal;

    content: "\f127";
  }
}

.srg-menu__settings-button .vjs-icon-placeholder {
  @extend .vjs-icon-cog;
  cursor: pointer;
}

.srg-menu__settings-button {
  $properties: transform, width, height;
  @include bouncyTransition($properties : $properties);
}

.srg-menu__container {
  position: absolute;

  right: 4px;
  bottom: 4px;

  width: auto;

  padding: 10px;

  background: transparent;

  border-radius: 4px;

  transform: scale(0.16);
  transform-origin: right bottom;
  opacity: 0;


  $properties: transform, transform-origin, background;
  @include bouncyTransition($properties : $properties);
}

.srg-menu__container__panels {
  width: max-content;
  width: intrinsic;
  width: -moz-max-content;
  width: -webkit-max-content;

  padding-bottom: 40px;

  font-size: 1.3em;
}

.srg-menu__close-button {
  display: none;

  align-self: end;
  order: 0;

  width: auto;

  font-size: 1.5em;
}

.srg-menu__panel-item__label,
.srg-toggle-panel__label {
  margin-right: 40px;
}

.srg-menu--open {
  .srg-menu__settings-button {
    transform: rotate(30deg);
  }

  .srg-menu__subtitles-button .vjs-icon-placeholder {
    @extend .vjs-icon-subtitles-active;
  }

  .srg-menu__container {
    background: rgba(22, 22, 22, 0.95);
    transform: scale(1);

    opacity: 1;
  }
}

//Menu subtitles
.srg-menu__subtitles-button .vjs-icon-placeholder {
  @extend .vjs-icon-subtitles;
  cursor: pointer;
}

.srg-menu__subtitles-button--active .vjs-icon-placeholder {
  @extend .vjs-icon-subtitles-active;
}

.srg-menu__subtitles-button {
  $properties: transform, width, height;
  @include bouncyTransition($properties : $properties);
}

&.srgssr-touch-enabled {
  .srg-menu {
    z-index: 2;
  }

  .srg-menu--open {
    z-index: 3;
  }

  .srg-menu__container {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;


    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;
    height: 100%;

    padding: 0;

    transform: scale(0);
  }

  .srg-menu--open .srg-menu__container {
    transform: none;
  }

  .srg-menu__close-button {
    position: absolute;
    top: 18px;
    right: 18px;
    z-index: 1;

    display: block;
  }

  .srg-menu__container__panels {
    display: flex;
    flex-direction: column;
    width: 325px;
    padding: 30px 16px 0 16px;
  }

  .srg-menu__panel-header {
    margin-bottom: 25px;
  }

  .srg-menu__panel {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;

    box-sizing: border-box;

    padding: 2px;

    animation: menu-repaint 1ms;
  }

  .srg-menu__panel-item {
    margin-bottom: 26px;
  }

  .srg-menu__panel-item:last-child {
    margin-bottom: 0;
  }

  .srg-menu__panel-item--list {
    display: grid;
    grid-column-gap: 14px;
    grid-template-columns: repeat(3, 1fr);

    width: auto;
    max-height: unset;

    margin-right: 0;

    overflow-y: hidden;

    button {
      margin: 0 13px 20px 13px;
    }
  }

  .srg-menu__subtitles-size-selector {
    font-size: 17px;

    button {
      padding: 0 10px;
    }
  }

  @keyframes menu-repaint {
    from {
      width: 99.999%
    }

    to {
      width: 100%
    }
  }   
}

//Picture in picture
.vjs-picture-in-picture-control {
  order: 3;
}

// Seek
.vjs-control-seek {
  display: none;

  cursor: pointer;
}

//Playback rate
.playback-rate-menu {
  display: none;
  justify-content: flex-end;
  width: 6em;
}
.srg-menu__playback-rate-button{
  width: auto;
  padding-right: 10px;
}

.vjs-playback-rate-value {
  padding: 0;
}

.srg-menu__playback-rate-button, .vjs-playback-rate-value {
  cursor: pointer;
}

//Audio Layout
&.srgssr-audio-source {
  .vjs-picture-in-picture-control,
  .vjs-fullscreen-control,
  .settings-menu,
  .srg-menu {
    display: none;
  }
  &:not(.vjs-live) .vjs-control-seek {
    display: block;
  }

  .playback-rate-menu{
    display: flex;
  }

  .vjs-playback-rate-value {
    font-size: 1.6em;
    font-family: srgssr-type-md;
  }
}
