.vjs-srgssr-button {
  order: 4;
  width: 66px;

  margin-right: 10px;
  padding-left: 10px;
}

$prefix: vjs-srgssr-;

.#{$prefix} {
  &rts {
    @include getBuSVG('../assets/svg/play-rts.svg');
  }

  &srf {
    @include getBuSVG('../assets/svg/play-srf.svg');
  }

  &rsi {
    @include getBuSVG('../assets/svg/play-rsi.svg');
  }

  &rtr {
    @include getBuSVG('../assets/svg/play-rtr.svg');
  }

  &swi {
    @include getBuSVG('../assets/svg/play-swi.svg');
  }
}
