@mixin getBuSVG($path) {
  .vjs-icon-placeholder:before,
  .vjs-icon-placeholder {
    font-weight: normal;
    font-family: VideoJS;
    font-style: normal;
  }
  .vjs-icon-placeholder:before {
    content: url($path);
  }
}

@mixin getErrorIconSVG($value) {
  &:before {
    position: absolute;
    top: 40%;
    left: 0;

    width: 100%;
    margin-top: -0.5em;

    color: #fff;
    font-weight: normal;
    font-size: 4em;
    font-family: VideoJS;
    font-style: normal;
    line-height: 1;
    text-align: center;
    text-shadow: 0.05em 0.05em 0.1em #000;
    vertical-align: middle;

    content: $value;
  }
}

@mixin setTransitionToBottom($visibility, $opacity, $bottom) {
  transition: visibility $visibility, opacity $opacity, bottom $bottom;
}

@mixin setTransitionToTop($visibility, $opacity, $top) {
  transition: visibility $visibility, opacity $opacity, top $top;
}

@mixin bouncyTransition($duration: .3s,
  $timing-function: cubic-bezier(.47, 1.55, .5, 1),
  $properties: null) {
  transition-timing-function: $timing-function;
  transition-duration: $duration;
  transition-property: $properties;
}

