.srgssr-continuous-playback {
  display: flex;
}

.continuous-playback__container {
  position: absolute;
  top: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;

  padding-bottom: $controlBarHeight;

  background-color: rgba(0, 0, 0, 0.8);
}

.continuous-playback__title {
  margin: .3em 0;

  font-size: calc(3em + 0.5vw);
}

.continuous-playback__coming-next,
.continuous-playback__description,
.continuous-playback__duration {
  color: rgb(160, 160, 160);

  font-size: calc(1em + 0.5vw);
  text-align: center;
}

.continuous-playback__description {
  margin: .2em 0;
}

//Countdown Button Component
.srgssr-circular-countdown {
  width: auto;
  margin: 1em 0;

  font-size: calc(1em + 0.5vw);
}

.circle-countdown {
  transform: rotateY(-180deg) rotateZ(-90deg);
}

.circle-red,
.circle-white {
  stroke-linecap: round;
  stroke-width: 2px;

  fill: none;
}

.circle-red {
  stroke-dasharray: 144.51326206513048px;

  stroke: #AC0000;
  stroke-width: 3px;

}

.circle-white {
  stroke: white;
}

@keyframes countdown {
  from {
    stroke-dashoffset: 0px;
  }

  to {
    stroke-dashoffset: 144.51326206513048px;
  }
}

//Cancel Button Component
.srg-cancel-button {
  width: auto;

  font-weight: bold;
  font-size: 1.4em;

  cursor: pointer;
}

&.vjs-layout-x-small,
&.vjs-layout-small,
&.vjs-layout-medium,
.continuous-playback--small {
  .continuous-playback__container {
    font-size: 14px;
  }

  .continuous-playback__title {
    font-size: 1.5em;
  }

  .continuous-playback__coming-next,
  .continuous-playback__description,
  .continuous-playback__duration,
  .srg-cancel-button {
    font-size: 1em;
  }

  .srgssr-circular-countdown {
    margin: .7em 0;
  }
}

&.vjs-touch-enabled.srgssr-has-continuous-playback{
  .vjs-control-bar,
  .continuous-playback__duration{
    display: none;
  }

  .continuous-playback__container{
    padding:0;
  }
}
